export default {
  name: "topBar",
  data() {
    return {
        noArrow: true,
        title: ''
    };
  },
  methods: {
    back(){
      console.log()
      if(this.$route.meta.appBack){
         this.$native.back()
      }else{
         this.$router.go(-1)
      }
    }
  },
  created () {
    this.noArrow = this.$route.meta.noArrow
        this.title = this.$route.meta.title
  },
  watch: {
    '$route'(){
        this.noArrow = this.$route.meta.noArrow
        this.title = this.$route.meta.title
    }
  }
}



